.general {
  position: relative;
}

.input {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 12px 20px;
  width: 300px;
  border-radius: 10px;
  font-size: 16px;

  &:focus {
    border: 1px solid rgba(0, 0, 0, 0.3);
  }
}

.closeIcon {
  width: 20px;
  height: 20px;
  opacity: 0.3;
  position: absolute;
  right: 15px;
  top: 14px;

  &:hover {
    opacity: 0.8;
    cursor: pointer;
  }
}
